import React, { useState } from "react";
import "./Header.css";
import { FiX, FiMenu } from "react-icons/fi";
import { Link } from "react-scroll";
import Phone from "../../images/icons/phone.png";
import Email from "../../images/icons/email.png";
import whatsapp from "../../images/icons/whatsapp.png";
import Logo from "../../images/logo-4.png"

const Header = () => {
  let [state, setstate] = useState(false);
  let handleclick = () => {
    setstate(!state);
  };

  return (
    <section class="header">
      <div className="container">
        <div className="header_logo">
          {/* <h1 className=""><span>Web</span>Developer</h1> */}
          <img src={Logo}/>

        </div>
        <div
          className={
            state ? "header_nav_links d-flex active sf" : "header_nav_links d-flex sf"
          }
        >
          <Link
            activeClass="active"
            to="hero_section"
            smooth={true}
            duration={500}
            className="anchor"
            onClick={handleclick}
          >
            Home
          </Link>
          <Link
            activeClass="active"
            to="about_me"
            smooth={true}
            duration={500}
            className="anchor"
            onClick={handleclick}
          >
            About
          </Link>
          <Link
            activeClass="active"
            to="skills"
            smooth={true}
            duration={500}
            className="anchor"
            onClick={handleclick}
          >
            Skills
          </Link>
          <Link
            activeClass="active"
            to="resume"
            smooth={true}
            duration={500}
            className="anchor"
            onClick={handleclick}
          >
            Resume
          </Link>
          <Link
            activeClass="active"
            to="projects"
            smooth={true}
            duration={500}
            className="anchor"
            onClick={handleclick}
          >
            Projects
          </Link>

          <Link
            activeClass="active"
            to="contactForm"
            smooth={true}
            duration={500}
            className="anchor"
            onClick={handleclick}
          >
            Contact
          </Link>
          <div className="row social_link d-md-none text-center pt-4">
            <div className="col-12 p-0">
              <a href="tel:+971 50 906 2829">
                <img src={Phone} alt="phone" />
              </a>
              <a href="https://wa.me/9047599627" target="_blank">
                <img src={whatsapp} alt="email" />
              </a>
              <a href="mailto:shabeerbabu.in@gmail.com" target="_blank">
                <img src={Email} alt="email" />
              </a>
            </div>
          </div>
        </div>
        <div className="nav_icon d-md-none">
          <div className="icons" onClick={handleclick}>
            {state ? <FiX /> : <FiMenu />}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Header;
