import React, { useEffect } from "react";
import "./skills.css";

import React_logo from "../../images/skills/react.png";
import HTML_logo from "../../images/skills/html.png";
import CSS_logo from "../../images/skills/css.png";
import Bootstrap_logo from "../../images/skills/bootstrap.png";
import JS_logo from "../../images/skills/js.png";
import Jquery_logo from "../../images/skills/jq.png";
import Php_logo from "../../images/skills/php.png";
import Node_logo from "../../images/skills/node_js.png";
import SQL_logo from "../../images/skills/sql.png";

import { ProgressBar } from "react-bootstrap";

//swiper
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, EffectCube } from "swiper";

import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "swiper/css/effect-cube";

// animation on scroll
import AOS from "aos";
import "aos/dist/aos.css";

const Skills = () => {
  useEffect(() => {
    AOS.init({});
  }, []);

  return (
    <section id="skills">
      <div className="container">
        <h2 className="heading" data-aos="flip-left" data-aos-duration="1000">
          Professional Skills
        </h2>
        {/* <h3 className="sub_heading">My Skills</h3> */}
        <div
              className="slider_container"
              data-aos="fade-up"
              data-aos-duration="800"
            >
              <Swiper
                autoplay={{
                  delay: 0,
                  disableOnInteraction: false,
                }}
                effect={"cube"}
                grabCursor={true}
                loop={true}
                speed={1500}
                cubeEffect={{
                  shadow: true,
                  slideShadows: true,
                  shadowOffset: 20,
                  shadowScale: 0.94,
                }}
                pagination={true}
                modules={[Autoplay, EffectCube]}
                className="mySwiper pb-5"
              >
                <SwiperSlide>
                  <img src={HTML_logo} alt="Html Logo" />
                </SwiperSlide>
                <SwiperSlide>
                  <img src={CSS_logo} alt="Css Logo" />
                </SwiperSlide>
                <SwiperSlide>
                  <img src={JS_logo} alt="Javascript Logo" />
                </SwiperSlide>
                <SwiperSlide>
                  <img src={Bootstrap_logo} alt="Bootstrap Logo" />
                </SwiperSlide>
                <SwiperSlide>
                  <img src={React_logo} alt="React Logo" />
                </SwiperSlide>
                <SwiperSlide>
                  <img src={Jquery_logo} alt="Jquery Logo" />
                </SwiperSlide>
                <SwiperSlide className="be_logo">
                  <img src={Php_logo} alt="Php Logo" />
                </SwiperSlide>
                <SwiperSlide className="be_logo">
                  <img src={Node_logo} alt="Node Logo" />
                </SwiperSlide>
              </Swiper>
        </div>
        <div className="row">
          <h4 className="text-center sub_title_skills mt-4 sf">
            Front-End Skills
          </h4>
          <div className="col-12 mx-2 d-flex justify-content-between text-center flex-wrap">
            <div className="col">
              <img src={HTML_logo} alt="Html Logo" />
            </div>
            <div className="col">
              <img src={CSS_logo} alt="Css Logo" />
            </div>
            <div className="col">
              <img src={JS_logo} alt="Javascript Logo" />
            </div>
            <div className="col">
              <img src={Bootstrap_logo} alt="Bootstrap Logo" />
            </div>
            <div className="col">
              <img src={React_logo} alt="React Logo" />
            </div>
            <div className="col">
              <img src={Jquery_logo} alt="Jquery Logo" />
            </div>
          </div>
          <h4 className="text-center sub_title_skills sf">Back-End Skills</h4>

          <div className="col-12 mx-2 d-flex justify-content-around align-items-center text-center flex-wrap">
            <div className="col">
              <img src={Php_logo} alt="Php Logo" className="b_skill" />
            </div>
            <div className="col">
              <img src={Node_logo} alt="Node Logo" className="b_skill" />
            </div>
            <div className="col">
              <img src={SQL_logo} alt="SQL" className="b_skill" />
            </div>
          </div>
          
        </div>

        <div className="row">
          <div
            className="col-12 col-lg-6"
            data-aos="zoom-in-right"
            data-aos-duration="600"
          >
            
            <div className="skills_discription body_paras points">
              <ul>
                <li>
                  Proficient in HTML, CSS, JavaScript, jquery, php, sql etc.
                </li>
                <li>Creating responsive and mobile-friendly websites.</li>
                <li>
                  Creating interactive user interfaces using React and jQuery
                </li>
                <li>SEO Optimization</li>
                <li>Skilled in REST API data integration.</li>
                <li>
                  Proficient in debugging front-end code for cross-browser and
                  UI issues using browser tools.
                </li>
              </ul>
            </div>
          </div>

          <div
            className="col-12 col-lg-6 sf"
            data-aos="zoom-in-left"
            data-aos-duration="600"
          >
            <div className="skills_content d-flex">
              <div className="logo">
                <img src={HTML_logo} alt="HTML" />
              </div>
              <div className="skills_progress_bar">
                <label>HTML</label>
                <ProgressBar now={95} />
                <span>95%</span>
              </div>
            </div>
            <div className="skills_content d-flex">
              <div className="logo">
                <img src={CSS_logo} alt="CSS" />
              </div>
              <div className="skills_progress_bar">
                <label>CSS</label>
                <ProgressBar now={92} />
                <span>92%</span>
              </div>
            </div>

            <div className="skills_content d-flex">
              <div className="logo">
                <img src={Bootstrap_logo} alt="BOOTSTRAP" />
              </div>
              <div className="skills_progress_bar">
                <label>Bootstrap</label>
                <ProgressBar now={90} />
                <span>90%</span>
              </div>
            </div>

            <div className="skills_content d-flex">
              <div className="logo">
                <img src={Jquery_logo} alt="JQUERY" />
              </div>
              <div className="skills_progress_bar">
                <label>Jquery</label>
                <ProgressBar now={85} />
                <span>85%</span>
              </div>
            </div>

            <div className="skills_content d-flex">
              <div className="logo">
                <img src={JS_logo} alt="JS" />
              </div>
              <div className="skills_progress_bar">
                <label>JavaScript</label>
                <ProgressBar now={80} />
                <span>80%</span>
              </div>
            </div>

            <div className="skills_content d-flex">
              <div className="logo">
                <img src={React_logo} alt="HTML" />
              </div>
              <div className="skills_progress_bar">
                <label>React Js</label>
                <ProgressBar now={65} />
                <span>65%</span>
              </div>
            </div>

            <div className="skills_content d-flex">
              <div className="logo">
                <img src={SQL_logo} alt="SQL" className="b_p_skill mt-2" />
              </div>
              <div className="skills_progress_bar">
                <label>SQL</label>
                <ProgressBar now={60} />
                <span>60%</span>
              </div>
            </div>

            <div className="skills_content d-flex">
              <div className="logo">
                <img src={Php_logo} alt="PHP" className="b_p_skill mt-2" />
              </div>
              <div className="skills_progress_bar">
                <label>Php</label>
                <ProgressBar now={50} />
                <span>50%</span>
              </div>
            </div>

            <div className="skills_content d-flex">
              <div className="logo">
                <img src={Node_logo} alt="NODE" className="b_p_skill" />
              </div>
              <div className="skills_progress_bar">
                <label>Node Js</label>
                <ProgressBar now={25} />
                <span>25%</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Skills;
