import React, { useEffect } from "react";
import "./HeroSection.css";

import Resume from "../../images/personal/shabeer-resume.pdf";

import AOS from "aos";
import "aos/dist/aos.css";

const HeroSection = () => {
  useEffect(() => {
    AOS.init({
      duration: 900, // animation duration in milliseconds
    });
  }, []);

  return (
    <section id="hero_section">
      <div className="container">
        <div className="personal_details">
          <h1 className="name" data-aos="fade-down">
            Shabeer Basha Babu
          </h1>
          <div className="work" data-aos="fade-left">
            Front End Developer
          </div>
          <div className="btns">
            <button data-aos="fade-down" data-aos-duration="1000">
              <a
                href={Resume}
                download = "Shabeer Basha-Resume"
                className="download_btn"
              >
                Download CV
              </a>
            </button>

            {/* <Link
                activeClass="active"
                to="projects"
                smooth={true}
                duration={1000}
                className="anchor"
                // onClick={handleclick}
              >
              <button
                data-aos="fade-left"
                data-aos-duration="1500"
                data-aos-offset="100"
              >
                
                  Projects
              </button>
            </Link> */}

          </div>
        </div>
        <div className="bg_img">{/* <img src={Bg} /> */}</div>
      </div>
    </section>
  );
};

export default HeroSection;
