import React, { useEffect, useState } from "react";
import "./AboutMe.css";
// import Img from "../../images/personal/per_img.jpeg";
import AboutMeImg from "../../images/personal/img-1.jpg";

import AOS from "aos";
import "aos/dist/aos.css";

const About = () => {
  useEffect(() => {
    AOS.init({});
  }, []);

  let [showMore, setShowMore] = useState(false);
  const handleClick = () => {
    setShowMore(!showMore);
  };

  return (
    <section id="about_me">
      <div className="container about_section">
        <h2 className="heading pb-3" data-aos="zoom-in" data-aos-duration="600">
          About
        </h2>

        <div className="row">
          <div
            className="col-12 col-lg-5"
            data-aos="zoom-in-right"
            data-aos-duration="600"
          >
            <div className="per_img">
              <img src={AboutMeImg} alt="Shabeer Image"/>
            </div>
          </div>
          <div
            className="col-12 col-lg-7 d-flex flex-column justify-content-center"
            data-aos="zoom-in-left"
            data-aos-duration="600"
          >
            <h3 className="sub_heading pb-3">About Me</h3>
            <div className="discription">
              <p className="body_paras">
                I am a skilled and professional Front-End developer with one year experience in
                developing Front-End as well as Back-End web development. I have extensive experience in developing 
                complete admin panels for dynamically functioning full-stack websites.
              </p>
              <div className="collapse" id="demo">
                <p className="body_paras">
                  I am passionate about keeping up with the latest front-end
                  technologies, and quickly adapt to new technologies efficiently.
                </p>
                <p className="body_paras">
                  I am equally comfortable working independently or as part of a
                  team, and I value collaboration and communication to deliver
                  high-quality results within timeline constraints.
                </p>
                <p className="body_paras">
                  I am excited about the opportunity to take on new challenges
                  and contribute my skills to the success of any team or
                  project.
                </p>
              </div>
            </div>
            <div className="btns">
              <button
                data-aos="fade-right"
                data-aos-duration="500"
                data-bs-toggle="collapse"
                data-bs-target="#demo"
                onClick={handleClick}
              >
                {showMore ? "Read less" : "Read more"}
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
